select {
  /* styling */
  background-color: white;
  /* border: thin solid blue;
  border-radius: 4px; */
  border: none;
  display: inline-block;
  font: inherit;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
  /* reset */
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none; }

/* arrows */
select.classic {
  background-image: -webkit-linear-gradient(45deg, transparent 50%, black 50%), -webkit-linear-gradient(315deg, black 50%, transparent 50%), -webkit-linear-gradient(left, white, white);
  background-image: -o-linear-gradient(45deg, transparent 50%, black 50%), -o-linear-gradient(315deg, black 50%, transparent 50%), -o-linear-gradient(left, white, white);
  background-image: linear-gradient(45deg, transparent 50%, black 50%), linear-gradient(135deg, black 50%, transparent 50%), linear-gradient(to right, white, white);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat; }

select.classic:focus {
  background-image: -webkit-linear-gradient(45deg, black 50%, transparent 50%), -webkit-linear-gradient(315deg, transparent 50%, black 50%), -webkit-linear-gradient(left, white, white);
  background-image: -o-linear-gradient(45deg, black 50%, transparent 50%), -o-linear-gradient(315deg, transparent 50%, black 50%), -o-linear-gradient(left, white, white);
  background-image: linear-gradient(45deg, black 50%, transparent 50%), linear-gradient(135deg, transparent 50%, black 50%), linear-gradient(to right, white, white);
  background-position: calc(100% - 15px) 1em, calc(100% - 20px) 1em, 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat; }

select:-moz-focusing {
  color: transparent;
  text-shadow: 0 0 0 #000; }

.top-nav form:after {
  content: ''; }

