.avatarupload {
  position: relative;
  max-width: 100px;
}
.avatarupload .avataredit {
  /* position: absolute;
        right: 12px;
        z-index: 1;
        top: 10px; */
}
.avatarupload .avataredit input {
  display: none;
}
.avatarupload .avataredit input + label {
  display: inline-block;
  width: 34px;
  height: 34px;
  margin-bottom: 0;
  border-radius: 100%;
  background: #FFFFFF;
  border: 1px solid transparent;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  cursor: pointer;
  font-weight: normal;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.avatarupload .avataredit input + label:hover {
  background: #f1f1f1;
  border-color: #d6d6d6;
}
.avatarupload .avataredit input + label:after {
  content: "\f040";
  font-family: 'FontAwesome';
  color: #757575;
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
}
.avatarupload .avatarpreview {
  width: 60px;
  height: 60px;
  position: relative;
  border-radius: 100%;
  border: 2px solid #F8F8F8;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
}
.avatarupload .avatarpreview > div {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}



